@import 'custom.scss';
// https://codepen.io/marcusconnor/pen/QJNvMa

.rocker {
  display: inline-block;
  position: relative;
  font-size: 1em;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  width: 8em; //7em
  min-width: 8rem;
  height: 4em;
  overflow: hidden;
  border-bottom: 0.5em solid #eee;
  user-select: none;
}

.rocker::before {
  content: '';
  position: absolute;
  top: 0.5em;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #999;
  border: 0.5em solid #eee;
  border-bottom: 0;
}

.rocker input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch-left,
.switch-right {
  cursor: pointer;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  width: 3.5em; //3em
  transition: 0.2s;
}

.switch-left {
  height: 2.4em;
  width: 3.25em; //2.75em
  left: 0.85em;
  bottom: 0.4em;
  background: $orange-gradient;
  transform: rotate(15deg) skewX(15deg);
}

.switch-right {
  right: 0.5em;
  bottom: 0;
  background: $green-gradient;
  color: #fff;
}

.switch-left::before,
.switch-right::before {
  content: '';
  position: absolute;
  width: 0.4em;
  height: 2.45em;
  bottom: -0.45em;
  background-color: #ccc;
  transform: skewY(-65deg);
}

.switch-left::before {
  left: -0.4em;
}

.switch-right::before {
  right: -0.375em;
  background-color: transparent;
  transform: skewY(65deg);
}

input:checked + .switch-left {
  background-color: #0084d0;
  color: #fff;
  bottom: 0px;
  left: 0.5em;
  height: 2.5em;
  width: 3.5em; //3em
  transform: rotate(0deg) skewX(0deg);
}

input:checked + .switch-left::before {
  background-color: transparent;
  width: 3.0833em;
}

input:checked + .switch-left + .switch-right {
  background-color: #ddd;
  color: #fff;
  bottom: 0.4em;
  right: 0.8em;
  height: 2.4em;
  width: 3.25em; //2.75em
  transform: rotate(-15deg) skewX(-15deg);
}

input:checked + .switch-left + .switch-right::before {
  background-color: #ccc;
}

/* Keyboard Users */
input:focus + .switch-left {
  color: #eee;
}

input:checked:focus + .switch-left {
  color: #fff;
}

input:focus + .switch-left + .switch-right {
  color: #fff;
}

input:checked:focus + .switch-left + .switch-right {
  color: #eee;
}
